/* src/index.css */
@tailwind base;
@tailwind components;
@tailwind utilities;

/* Additional custom styles */
html, body {
  @apply antialiased;
}

/* For the fade-in animation if not using the jsx style tag */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(-10px); }
  to { opacity: 1; transform: translateY(0); }
}
.animate-fadeIn {
  animation: fadeIn 0.3s ease-out forwards;
}